<template>
  <div class="slider">
    <div class="slider__row">
      <div class="slider__item h100" v-for="(item, index) in items">
        <div class="row h100">
          <div class="image h100" :style="{ backgroundImage: 'url(' + item.image + ')' }">
            <button class="button" @click="goto(index)">{{item.text}}</button>
          </div>
        </div>
      </div>
    </div>
    <div class="button_group">
      <button class="button button_left pull-left" @click="left">next</button>
      <button class="button button_right pull-right" @click="right">prev</button>
    </div>
  </div>
</template>

<script>
	export default {
		props: {
			items: {
				type: Object,
			},
		},
		methods: {
			left(){

			},
			right(){

			},
      sliderRowStyle(){
			  return {
			    'width': Object.keys(this.items).length / 3 * window.innerWidth
        }
      },
			goto(project){
				if (this.$route.path.indexOf("/idar") > -1) {
					this.$router.push('/' + this.$route.params.lang + '/idar/' + project);
				}
				else if (this.$route.path.indexOf("/fashion") > -1) {
					this.$router.push('/' + this.$route.params.lang + '/fashion/' + project);
				}
			},
      mounted(){
			  console.log('width:' + Object.keys(this.items).length / 3 * window.innerWidth);
      }
		}
	}
</script>

<style lang="sass" src="./../../assets/style/idar__gallery.sass"></style>


